@Composable
expect fun Menu(
    modifier: Modifier = Modifier,
)

@OptIn(ExperimentalMaterial3Api::class)
@Composable
fun Options(
    isVisible: Boolean,
    options: List<ScreenBottom>,
    onChangeThemeClick: () -> Unit,
    screenContent: @Composable () -> Unit,
) {
    var selectedItem by remember { mutableIntStateOf(0) }
    Scaffold(
        topBar = {
            if (isVisible) {
                TopAppBar(
                    title = {
                        Menu()
                    },
                    actions = {
                        Button(onClick = onChangeThemeClick) {
                            Text(text = "Change Theme")
                        }
                    }
                )
            }
        },
        bottomBar = {
            if (isVisible) {
                BottomAppBar(
                    modifier = Modifier.fillMaxWidth(),
                    actions = {
                        options.forEachIndexed { index, screen ->
                            NavigationBarItem(
                                icon = {},
                                label = {
                                    Text(
                                        text = screen.title,
                                        style = MaterialTheme.typography.labelLarge,
                                        fontWeight = FontWeight.Light
                                    )
                                },
                                selected = selectedItem == index,
                                onClick = {
                                    selectedItem = index
                                    screen.onClick()
                                },
                                colors = NavigationBarItemDefaults.colors(
                                    selectedIconColor = MaterialTheme.colorScheme.primary
                                )
                            )
                        }
                    }
                )
            }
        },
        content = { paddingValues ->
            screenContent.invoke()
//            SideEffect {
//                bottomBarVisible = when (childStack.items.last().instance) {
//                    is RootComponent.Child.Excerpts,
//                    is RootComponent.Child.Home,
//                    is RootComponent.Child.Favorites,
//                    -> true
//
//                    else -> false
//                }
//            }
        }
    )
}