package domain.quote

import kotlinx.serialization.Serializable

@Serializable
data class QuotesResponse(val quotes: List<Quote>)

@Serializable
data class RandomQuoteResponse(val randomQuote: Quote)

@Serializable
data class Quote(
    val notionId: String,
    val authorName: String? = null,
    val title: String? = null,
    val text: String,
    val boldText: String? = null,
    val underlinedText: String? = null,
)