package presentation.home

import com.arkivanov.decompose.ComponentContext
import domain.PrestigeRemoteDataSource
import kotlinx.coroutines.SupervisorJob
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.flow.update
import kotlinx.coroutines.launch
import navigation.coroutineScope
import org.koin.core.component.KoinComponent
import org.koin.core.component.get
import kotlin.coroutines.CoroutineContext

class HomeComponent(
    componentContext: ComponentContext,
    mainContext: CoroutineContext,
) : ComponentContext by componentContext, KoinComponent {
    private val api: PrestigeRemoteDataSource = get()
    private val coroutineScope = coroutineScope(mainContext + SupervisorJob())

    private val _state = MutableStateFlow(HomeState())
    val state = _state.asStateFlow()

    init {
        coroutineScope.launch {
            _state.update { state ->
                val quote = api.getRandomQuote()
                state.copy(isLoading = false, randomQuote = quote)
            }
        }
    }

    fun onNext() {
        coroutineScope.launch {
            _state.update { state ->
                state.copy(isLoading = true)
            }
            _state.update { state ->
                val quote = api.getRandomQuote()
                state.copy(isLoading = false, randomQuote = quote)
            }
        }
    }
}
